/**
 * Re-maps a number from one range to another (Also known as scale or lerp)
 *
 * @param  {number} value  - The incoming value to be converted
 * @param  {number} b1 - Lower bound of the value's current range
 * @param  {number} s1  - Upper bound of the value's current range
 * @param  {number} b2 - Lower bound of the value's target range
 * @param  {number} s2  - Upper bound of the value's target range
 * @return {number} Remapped number
 */
function map ( v, b1, s1, b2, s2) {
    return ((v - b1) / (s1 - b1)) * (s2 - b2) + b2
}

export default map;
