/**
 * Converts a long-lat coordinate to an XYZ position
 *
 * @param {number} latitude - Latitude coordinate
 * @param {number} longitude - Longitude coordinate
 * @return {array} Returned in format [x,y,z]
 */
const DEGREE_TO_RADIAN = Math.PI / 180;

function coordPos(latitude, longitude, radius) {
	const phi = (90 - latitude) * DEGREE_TO_RADIAN;
	const theta = (longitude + 180) * DEGREE_TO_RADIAN;
	const x = -radius * Math.sin(phi) * Math.cos(theta);
	const y = radius * Math.cos(phi);
	const z = radius * Math.sin(phi) * Math.sin(theta);
	return [x, y, z];
}

export default coordPos;
